.about__container {
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    padding: 20px;
    border-radius: 5px;
    background-color: white;
    margin: 0 auto;
    margin-top: 300px;
    width: 100%;
    max-width: 400px;
    font-weight: 300;
    animation-duration: 1s;
    animation-name: about-slidein;
}

@keyframes about-slidein {
    from {
        opacity: 0;
        margin-top: 250px;
    }

    to {
        opacity: 1;
        margin-top: 300px;
    }
}

    .about__container span:first-child {
        font-size: 20px;
    }

    .about__container span:last-child {
        display: block;
        margin-top: 10px;
    }
